
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Quicksand", sans-serif;
  color: #333;
}
header {
  height: 70px;
}
header h1 {
  display: inline-block;
  float: left;
  padding: 15px 24px;
}
header h1 a {
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
header h1 a:hover {
  color: #07fcde;
}
header ul {
  float: right;
}
header ul li {
  padding: 24px;
  display: inline-block;
}
header ul li a {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
header ul li a:hover {
  color: #07fcde;
}
a {
  text-decoration: none;
}
.container {
  width: 90%;
  margin: 0 auto;
}
.container::after {
  content: "";
  display: block;
  clear: both;
}
.mobile-menu {
  float: right;
  display: none;
  padding: 18px;
}
.mobile-menu:hover {
  cursor: pointer;
}
@media (max-width: 768px){
.mobile-menu {
		display: block;
}
header ul {
		display: none;
}
}  

#check {
  display: none;
}
.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -300px;
  width: 300px;
  background-color: rgba(16, 47, 80, 0.9);
  -webkit-transition: 0.4s;
  transition: 0.4s;
  z-index: 9999;
}
.sidebar ul li a {
  color: #fff;
}
.sidebar ul li {
  padding: 24px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  -webkit-transition-property: background-color;
  transition-property: background-color;
}
.sidebar ul li:hover {
  background-color: #ff0000;
}
#check:checked ~ .sidebar {
  left: 0;
}

.banner {
  background-color: #08bdf7;
  padding: 150px 0;
}
section {
  padding: 50px 0;
}
.banner img {
  width: 100px;
  border-radius: 10px;
  -webkit-box-shadow: 0 3px 5px #ddd;
          box-shadow: 0 3px 5px #ddd;
  margin-bottom: 10px;
}
.banner h2,
.banner p,
.banner span {
  color: #fff;
}
.banner h2 {
  margin-bottom: 10px;
}
.banner p {
  font-size: 18px;
}
.banner-left {
  padding-left: 24px;
}
section h3 {
  padding-bottom: 20px;
  text-align: center;
  margin: 20px;
  position: relative;
}
section h3::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  width: 120px;
  height: 1px;
  background-color: #ddd;
  left: calc(50% - 60px);
}
section h3::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -1px;
  width: 40px;
  height: 4px;
  background-color: #0825f7;
  left: calc(50% - 20px);
}

#about p {
  text-align: justify;
  text-indent: 30px;
  line-height: 25px;
  margin-bottom: 15px;
}

#skills {
  background-color: #f9f9f9;
}
#skills .bar {
  height: 30px;
  background-color: #fff;
  border: 1px solid #ddd;
  margin: 5px 0 20px 0;
}
.bar .nilai {
  height: 30px;
  background-color: #333b78;
  display: inline-block;
  color: #fff;
  text-align: center;
}
.html {
  width: 100%;
}
.css {
  width: 80%;
}
.js {
  width: 80%;
}
.php {
  width: 90%;
}

.card {
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  margin: 50px;
  float: left;
  text-align: center;
  margin-bottom: 15px;
}
.card img {
  width: 100%;
}
.card a:hover img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
@media (max-width: 768px){
.col-2 {
		width:100%;
}
}

footer {
  background-color: #333;
  padding: 50px;
  text-align: center;
}
footer small {
  color: #fff;
}
